import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { DisplayMode } from '@microsoft/sp-client-base';

import { IWebPartProps } from './IWebPartProps';
import IWebPartContext from './IWebPartContext';
import BaseClientSideWebPart from './BaseClientSideWebPart';

/**
 * React web part props
 *
 * @alpha
 */
export interface IReactWebPartProps extends IWebPartProps {
  /**
   * The display mode for the summary links component. It is either Read or Edit.
   * This value is read from web part and passed to here.
   */
  mode: DisplayMode;
}

/**
 * Implement some common functionality for a base React webpart. All React based
 * webparts are expected to inherit from this class.
 *
 * @alpha
 */
export default class ReactWebPart<TProperties> extends BaseClientSideWebPart<TProperties> {
  /* tslint:disable:no-any */
  private _componentType: React.ComponentClass<any>;
  /* tslint:enable:no-any */
  private _componentInstance: React.ReactElement<IReactWebPartProps>;

  constructor(
    context: IWebPartContext,
    componentType: React.ComponentClass<IReactWebPartProps>) {

    super(context);
    this._componentType = componentType;
  }

  protected dispose(): void {
    ReactDOM.unmountComponentAtNode(this.domElement);
  }

  public render(): void {
    const element: HTMLElement = this.domElement;

    this._componentInstance = React.createElement<IReactWebPartProps>(
      this._componentType,
      {
        mode: this.displayMode
      });

    ReactDOM.render(this._componentInstance, element);
  }
}

